	_flag = false;
	_tooltipTimer = null;
	_echartsInstance = undefined;
	_defaultAreaIndex = -1;

	componentDidUpdate() {
		
	}

	_initEchartsOption(effectScatterData, linesData) {
		let me = this;

		let option = {
			tooltip: {
				show: true
			},
			geo: me.geo.geo,
			series: [{
					name: '',
					type: 'map',
					mapType: 'china',
					geoIndex: 1,
					z: 2,
					zlevel: 2,
					label: {
						normal: {
							show: true,
							color: '#fff',
							fontSize: 22
						},
						emphasis: {
							show: true,
							color: '#fff',
							fontSize: 22
						}
					},
					tooltip: {
						show: false
					},
					data: me.initData.slice()
				},
				{
					name: '',
					type: 'effectScatter',
					coordinateSystem: 'geo',
					zlevel: 3,
					z: 3,
					symbol: 'circle',
					//symbolOffset: [0, -18],
					itemStyle: {
						normal: {
							color: '#fefa92'
						}
					},
					rippleEffect: {
						period: 12,
						scale: 2.5,
						brushType: 'stroke'
					},
					label: {
						normal: {
							show: false,
							color: '#fff'
						}
					},
					tooltip: {
						show: true,
						trigger: 'item',
						formatter: function(d) {
							if(d.data.all) {
								return d.marker + d.data.all.name;
							} else {
								return d.marker + d.name;
							}
						}
					},
					data: effectScatterData
				}, {
					name: '',
					type: 'lines',
					zlevel: 4,
					effect: {
						show: true,
						period: 6,
						trailLength: 0.7,
						color: '#fff',
						symbolSize: 1
					},
					lineStyle: {
						normal: {
							color: '#22ff7f',
							width: 0,
							curveness: 0.2
						}
					},
					tooltip: {
						show: false,
						trigger: 'item'
					},
					data: linesData
				},
				{
					name: '',
					type: 'lines',
					zlevel: 5,
					symbol: ['none', 'none'],
					symbolSize: 10,
					effect: {
						show: true,
						period: 6,
						trailLength: 0.2,
						symbol: 'roundRect',
						symbolSize: [3, 8]
					},
					lineStyle: {
						normal: {
							color: '#22ff7f',
							width: 1,
							opacity: 0.6,
							curveness: 0.2
						}
					},
					tooltip: {
						show: false,
						trigger: 'item'
					},
					data: linesData
				}
			]
		};
		me._echartsInstance.showLoading();
		echarts.registerMap('china', chinaJson);
		me._echartsInstance.hideLoading();
		me._echartsInstance.setOption(option, true);

	}

