	componentWillUnmount() {
		let me = this;
		if(me._echartsInstance) {
			me._echartsInstance.dispose();
			me._echartsInstance = undefined;
		}
	}

	render() {
		let me = this;
		return(
			<div style={me.props.style}>
        <div ref='chinaMapRef' style={me.props.style}>
        </div>
      </div>
		);
	}
